  methods: {
    BackTop(){
      let timer=setInterval(() => {
        let speed=Math.floor(-this.top/5);
        document.documentElement.scrollTop=document.body.scrollTop=this.top+speed;
        if(this.top==0){
          clearInterval(timer);
        }
      }, 16);
    },
    addCar(arr){
      let currentList = this.$store.state.shopcarList;    
      let index = 0;
      for (const i in currentList) {
        if (currentList[i].id == arr.id) index = i; 
      }
      if (index) currentList[index].reduceFlag = true;      
      index ? currentList[index].thisNum++ : currentList.push(arr);
      this.$store.commit("changeList", currentList); 
    },
    useData(){
      this.$axios({
        url:"/text.json",
        methods:"get"
      }).then(res=>{
